* {
	margin: 0;
	padding: 0;
	box-sizing: border-box;
	color: white;
	font-family: 'Montserrat', sans-serif;
}
.App {
	text-align: center;
	background: linear-gradient(0deg, rgb(29, 31, 119) 5%, rgb(11, 18, 70) 68%);
	height: 100vh;
	width: 100%;
	display: flex;
	justify-content: center;
	align-items: center;
}
